import {
  ChangeDetectionStrategy,
  Component,
  Directive,
  EventEmitter,
  Input,
  Output,
} from '@angular/core';
import { StopCheck } from '@backbase/payment-orders-ang';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-stop-checks-list',
  templateUrl: './stop-checks-list.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class StopChecksListComponent {
  /**
   * Reference to instance of customizable component.
   */
  readonly hostRef = this;

  /**
   * The payment list.
   */
  @Input() checks?: StopCheck[];

  /**
   * Changes will be triggered on paymentItemClick event emit.
   */
  @Output() checkItemClick = new EventEmitter<StopCheck>();

  onPaymentItemClick(checkItem?: StopCheck) {
    if (checkItem) {
      this.checkItemClick.emit(checkItem);
    }
  }
}

@Directive({
  selector: '[bbStopChecksListCustomizableDirective]',
})
export class StopChecksListCustomizableDirective extends BbTemplate<
  StopChecksListComponent,
  undefined
> {}
